
































import itemPublic from '../../../components/item_public/item_public.vue'
export default {
	data(){
		return{
			titles:['全部','待支付','待收货','待评价','已完成'],
			arrent:0,
			puy_seccuss:1
		}
	},
	mounted:function(){
		
	},
	onLoad() {
		uni.showLoading({
			title:'加载中...'
		});
		setTimeout(function(){
			uni.hideLoading()
		},1000)
	},
	methods:{
		getIndex(i){
			this.arrent = i;
			if(i == 0){
				this.puy_seccuss = 1;
			}else{
				this.puy_seccuss = i;
			}
			 
		},
		navigateTo(){
			uni.navigateTo({
				url:'../tui_item/tui_item'
			})
		},
		toDetail(){
			uni.navigateTo({
				url:'../item_detail/item_detail'
			})
		},
		goPay(){
			uni.navigateTo({
				url:'../pay/pay'
			})
		}
	},
	components:{
		itemPublic
	}
}



